@import 'src/scss/colors.scss';

.creation-content{
  .form-field{
    margin-bottom: 10px;

    &.inline-div{
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px;
    }
    .input-group{
      margin-top: 10px;
      input{
        border-radius: 12px;
        border: none;
        padding: 10px 10px 10px 40px;
      }
      .input-icon{
        position: absolute;
        top: 8px;
        left: 10px;
        z-index: 4;
        line-height: 20px;
      }
    }

    .image-wrapper{
      position:relative;
      .sponsor-img{
        max-height: 40px;
        max-width: 110px;
      }
    }
  }

  .belongs-wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;

    .belongs-bloc{
      display: flex;
    }
    .form-check{
      padding: 10px;
      padding-left: 30px;
      border: 0.5px solid $noir;
      box-sizing: border-box;
      border-radius: 12px;
      color: $noir;
    }
  }

  .upload-btn{
    position: relative;
  }
  [type=file] {
    z-index:2;
    cursor: pointer;
    display: block;
    filter: alpha(opacity=0);
    height: 100%;
    width: 100%;
    opacity: 0;
    position: absolute;
    right: 0;
    text-align: right;
    top: 0;
  }

  .creator-wrapper{
    display: flex;
    border: 0.5px solid $grisFonce;
    border-radius: 12px;
    &:not(:empty){
      padding: 20px;
      margin-bottom: 10px;
    }
    .img-circle{
      max-width: 100px;
      max-height: 100px;
    }
    p{
      margin-bottom: 10px;
    }
  }
}