@import 'src/scss/colors.scss';
@import 'src/scss/global.scss';

.homepage{
  flex: 1 1;
  color: $noir;
  background: $gris;
  max-width: 100%;
  overflow: scroll;

  .link{
    text-decoration: underline;
  }

  section{
    min-height: 750px;
    padding-top: 100px;
    padding-bottom: 93px;
    position: relative;
    overflow: hidden;
  }

  #hero {
    background-image: url(../../assets/figma/integration_illu.svg);
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-top: 150px;
    margin-top: -60px;
    height: 772px;

    .search-col{
      padding-left: 20px;
      font-size: 22px;
      font-weight: bold;
      color: $noir;
      display: flex;
      align-items: center;
      .mr-10{
        padding-right: 10px;
      }
      .search-btn{
        font-size: 22px;
      }
      .search-options{
        font-size: 18px;
      }
    }
  }

  .triptique{
    padding-top: 50px;
  }

  .hero-container{
    text-align: center;

    h5{
      margin-top: 10px;
      font-weight: normal;
    }

    .search-row{
      margin: 25px auto 0;
      display:flex;
      align-items: center;
      justify-content: space-between;
      background: rgba(255, 255,255, .9);
      box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.25);
      border-radius: 12px;
      width: fit-content;
    }
  }

  .search-btn{
    -webkit-animation: on-homepage 0.4s cubic-bezier(0.455, 0.030, 0.515, 0.955) infinite both;
	          animation: on-homepage 0.4s cubic-bezier(0.455, 0.030, 0.515, 0.955) infinite both;
  }

  // .header-anchor{
  //   margin-top: -60px;
  //   padding-top: 60px;
  // }

  // .arrowhead-icon{
  //   position: absolute;
  //   bottom: 100px;
  // }
  
  .section-container{
    width: 100%;
    padding: 0 100px;
    height: 100%;
    @include media-max("sm-limit") { padding: 0 20px; }

    &.half-width{
      width: 50%;
      display: flex;
      flex-direction: column;
      @include media-max("tablet-down") { width: 100%; }
      h2{
        margin-bottom: 40px;
      }

      &.right-side{
        margin-left: auto;

      }
    }
    .section-body{
      flex: 1 1 auto;
    }

    .footer-section{
      font-size: 18px;
      margin-right: -20px;
    }
  }
  .card{
    background-color: initial; 
  }

  #contribution{
    background-image: url(../../assets/figma/contribution_illu.svg), url(../../assets/figma/trame_home_bg.svg);
    background-size: 65%, cover;
    background-repeat: no-repeat;
    background-position: calc(100% + 20px) calc(100% - 160px), 0;
    @include media-max("tablet-down") { background-position: calc(100% + 20px) calc(100% - 110px), 0; }
  }
  
  #certifiee{
    background-image: url(../../assets/figma/certifiee_illu.svg), url(../../assets/figma/trame_home_bg.svg);
    background-size: 65%, cover;
    background-repeat: no-repeat, no-repeat;
    background-position: calc(100% - 60px) calc(100% - 120px),0;
  }

  #multilangues{
    background-image: url(../../assets/figma/illustration_Traduction.svg);
    background-size: 70%;
    background-repeat: no-repeat;
    background-position: calc(100% - 350px);
    @include media-max("desktop-down") { background-position: calc(100% - 300px) calc(100% - 60px); }
    @include media-max("desktop-up") { background-position: calc(100% - 300px) calc(100% - 80px); }
    @include media-max("tablet-down") { background-position: calc(100% - 240px) calc(100% - 140px); }
    @include media-max("tablet-up") { background-position: calc(100% - 180px) calc(100% - 140px); }
  }

  #explique{
    background-image: url(../../assets/figma/illustration_Lexique.svg);
    background-size: 70%;
    background-repeat: no-repeat;
    background-position: calc(100% - 300px);
    @include media-max("desktop-down") { background-position: calc(100% - 300px) calc(100% - 60px); }
    @include media-max("desktop-up") { background-position: calc(100% - 300px) calc(100% - 80px); }
    @include media-max("tablet-down") { background-position: calc(100% - 240px) calc(100% - 140px); }
    @include media-max("tablet-up") { background-position: calc(100% - 180px) calc(100% - 140px); }
  }



  #plan{
    h2{
      margin-bottom: 42px;
    }
    .card-col{
      border-radius: 12px;
      .card{
        height: 520px;
        border-radius: 12px;
        border:none;
        padding: 40px;

        .card-header{
          background: transparent;
          border: none;
          font-weight: 900;
          font-size: 32px;
          line-height: 36px;
          height: 100px;
          padding:0;
          @include media-max("sm-limit") { font-size: 21px; }
        }
        .card-body{
          padding:0;
        }
        .card-footer{
          background: transparent;
          border: none;
          display: flex;
          justify-content: center;
          padding:0;
          font-weight: bold;

          .figma-btn{
            max-width: 100%;
            flex-wrap: wrap;
            text-overflow: ellipsis;
            white-space: inherit;
            overflow-wrap: break-word;
            word-break: break-word;
            min-height: 60px;
            justify-content: center;
          }
          .homebtn{
            background: rgba(255, 255, 255, 0.95);
            color: $noir;
            transition: all 0.5s ease;
            padding: 10px 20px;
            box-shadow: 0px 4px 40px rgba(0, 0, 0, 0.25);
            display: flex;
            align-items: center;
            &:hover{
              background: $noir;
              color: $blancSimple;
              box-shadow: 0px 4px 40px rgba(0, 0, 0, 0.25);
              svg{
                transition: all 0.5s ease;
                fill: $blancSimple;
              }
            }
            &:disabled{
              svg{
                fill: $blancSimple
              }
              background: $grisFonce;
              color: $blancSimple;
              &:hover{
                background: $grisFonce;
              };
            }
          }
        }
      }
    }
  }

  /*--------------------------------------------------------------
  # Search bar
  --------------------------------------------------------------*/
  .input-group.md-form.form-sm.form-2 {
    flex: 1;
    margin-right: 10px;
    border-radius: 12px;
    height: 100%;

    .react-autosuggest__container{
      width:100%;
      position: relative;
      flex: 1 1 auto;
      width: 1%;
      height: 100%;

      .react-autosuggest__input{
        width:100%;
        border-radius: 12px;
        border: 0.5px solid $grisFonce;
        height: 100%;
      }
    }
  }

  .input-group-text{
    background: $noir;
    border-radius: 0 12px 12px 0;
    color: #FFFFFF;
    font-size: 16px;
    font-weight: bold;
    border: 0.5px solid #3C3C3C;
    border-left: none;
  }

  .dispo-card{
    background-image: url(../../assets/figma/illustration_Dispositifs.svg);
    background-size: cover;
  }

  .demarche-card{
    background-image: url(../../assets/figma/illustration_Demarches.svg);
    background-size: cover;
  }

  .parcours-card{
    background-image: url(../../assets/figma/illustration_Parcours.svg);
    background-size: cover;
  }

  .chevron-wrapper{
    flex: 1;
    width: 100%;
    position: relative;
  }

  .background-wrapper{
    border-radius: 50%;
    background-color: $noir;
  }
  .slide-animation{
    position: absolute;
    width: 48px;
    height: 48px;
    bottom: -30px;
    left: 50%;
    background: $noir;
    border-radius: 50%;
    z-index: 1;
    transition: all 0.5s ease;
    -webkit-animation: slide-bottom 2s cubic-bezier(0.250, 0.460, 0.450, 0.940) infinite alternate both;
            animation: slide-bottom 2s cubic-bezier(0.250, 0.460, 0.450, 0.940) infinite alternate both;
  }

  .bottom-slider {
    position: absolute;
    fill: $blancSimple;
    z-index: 10;
    svg{
      margin-left: -6px;
      margin-top: -6px;
    }
    &:hover{
      svg{
        transition: all 0.5s ease;
        fill:$gris;
      }
    }
  }

  .flip-horizontal-top {
    -webkit-animation: flip-horizontal-top 0.4s cubic-bezier(0.455, 0.030, 0.515, 0.955) both;
            animation: flip-horizontal-top 0.4s cubic-bezier(0.455, 0.030, 0.515, 0.955) both;
  }
}

@-webkit-keyframes slide-bottom {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }
}

@keyframes slide-bottom {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }
}

@-webkit-keyframes flip-horizontal-top {
  0% {
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
  }
  100% {
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
  }
}
@keyframes flip-horizontal-top {
  0% {
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
  }
  100% {
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
  }
}
